            getNewComming() {
                this.axios.get(this.api.getNewsApi + "9")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.newComing = res.data.slice(0, 2)
                            // console.log(this.newComing)
                        }
                    })
            },
            // 获取加盟咨询
            getJoin() {
                this.axios.get(this.api.getNewsApi + "10")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.join = res.data
                            // console.log(this.join)
                        }
                    })
            },
            // 获取创业问答
            getQuestion() {
                this.axios.get(this.api.getNewsApi + "11")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.question = res.data
                            // console.log(this.question)
                        }
                    })
            },
            // 获取精选展会
            getExhibition() {
                this.axios.get(this.api.getNewsApi + "12")
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.exhibition = res.data
                            // console.log(this.exhibition)
                        }
                    })
            },
            // 获取美食攻略
            getFoods() {
                this.axios.get(this.api.getNewsApi + "13")
                    .then(res => {
                        // console.log(res.data)
                        // console.log(res.data.slice(2,3)[0].title.trim())
                        if (res.status === 200) {
                            this.foods = res.data
                            // console.log(this.foods)
                        }
                    })
            },
            // 获取随机新闻
            getRandomNews() {
                this.axios.get(this.api.getRandomNewsListApi)
                    .then(res => {
                        // console.log(res.data)
                        if (res.status === 200) {
                            this.randomNews = res.data
                            // console.log(this.randomNews)
                        }
                    })
            },

            getAll() {
                this.getHot()
                this.getDynamics()
                this.getNewComming()
                this.getJoin()
                this.getQuestion()
                this.getExhibition()
                this.getFoods()
                this.getRandomNews()
            },
            // 跳转详情
            toDetail(id) {
                // console.log(id)
                this.$router.push({
                    name: "newsDetails",
                    params: {
                        sid: id
                    }
                })
            }
        },
        mounted() {
            this.swiperInit("#swiper-container1");
            this.swiperInit("#swiper-container2");
        },
        created() {
            this.getAll();
        },
        components: {
            foot, tabNav, floating
        }
    }
</script>

